* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* img {
  width: 100%;
} */

body {
  background-color: rgb(167, 205, 240);
  font-family: "Open Sans", sans-serif;
  min-height: 100vh;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  margin: 1.5rem auto;
  width: 80vw;
  min-height: 100vh;
  /* outline: 3px solid blue; */
}

.content-heading {
  font-size: 34px;
  margin-bottom: 1rem;
  text-align: center;
}

.sub-heading {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  letter-spacing: 1px;
  margin-bottom: 1.5rem;
  text-align: center;
}

.form-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0.8rem auto 1.5rem;
  width: 70%;
}

.paragraphs {
  margin: 0 auto;
  width: 90%;
}

.each-paragraph {
  background-color: #e6f3fd;
  border-radius: 4px;
  font-size: 15px;
  margin: 1rem 0;
  padding: 15px 20px;
  letter-spacing: 1px;
  line-height: 1.5;
}

label {
  font-size: 15px;
  margin-right: 10px;
}

input {
  padding: 5px 10px;
  width: 60px;
}

button {
  background-color: rgb(255, 231, 92);
  border: 1px solid #777;
  border-radius: 3px;
  cursor: pointer;
  color: #555;
  display: block;
  margin: 0.5rem auto 1rem;
  padding: 6px 14px;
}

button:hover {
  color: #111;
  opacity: 0.8;
  transform: scaleX(1.05);
  transition: transform 0.2s ease-in;
}

.form-group {
  margin-bottom: 1rem;
}

/*  */

@media screen and (max-width: 720px) {
  .wrapper {
    width: 90vw;
  }
}

@media screen and (max-width: 360px) {
  .content-heading {
    font-size: 28px;
    margin-bottom: 0.5rem;
  }

  .sub-heading {
    font-size: 16px;
    margin-bottom: 0.5rem;
  }

  .form-group {
    margin-bottom: 0.5rem;
  }

  label,
  input {
    display: block;
    margin: 1rem auto;
  }
}
